.input > [role="input"] {
  cursor: pointer; }

.header {
  padding: 1.6rem 2rem;
  color: white;
  cursor: pointer;
  background-color: #90a4ae; }

.year {
  display: inline-block;
  font-size: 1.4rem;
  transition: opacity, font-size 0.35s cubic-bezier(0.4, 0, 0.2, 1); }

.date {
  display: block;
  font-weight: 500;
  text-transform: capitalize;
  transition: opacity 0.35s cubic-bezier(0.4, 0, 0.2, 1); }

.wrapper {
  padding: 1rem 0.5rem 0; }

.display-years .date {
  opacity: 0.6; }

.display-years .year {
  font-size: 1.6rem; }

.display-months .year {
  opacity: 0.6; }

.dialog {
  width: 33rem; }
  .dialog > [role="body"] {
    padding: 0; }
  .dialog > [role="navigation"] > .button {
    color: #90a4ae; }
    .dialog > [role="navigation"] > .button:hover {
      background: rgba(144, 164, 174, 0.2); }
    .dialog > [role="navigation"] > .button:focus:not(:active) {
      background: rgba(144, 164, 174, 0.2); }



