.root {
  padding: 1.5rem 2rem; }

.placeholder {
  position: relative;
  z-index: 100; }

.wrapper {
  position: absolute;
  width: 100%;
  background-color: #eeeeee;
  border-radius: 50%; }

.face {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 100;
  cursor: pointer;
  border-radius: 50%;
  transform: translateX(-50%) translateY(-50%); }

.number {
  position: relative;
  width: 2rem;
  height: 2rem;
  margin-top: -1rem;
  margin-left: -1rem;
  text-align: center;
  pointer-events: none;
  user-select: none; }
  .number.active {
    color: white; }

.hand {
  position: absolute;
  bottom: 50%;
  left: 50%;
  display: block;
  width: 0.4rem;
  margin-left: -0.2rem;
  background-color: #90a4ae;
  transform-origin: 50% 100%; }
  .hand:before {
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 1rem;
    height: 1rem;
    margin-bottom: -0.5rem;
    margin-left: -0.5rem;
    content: "";
    background-color: #90a4ae;
    border-radius: 50%; }
  .hand.small > .knob {
    background-color: rgba(144, 164, 174, 0.2); }
    .hand.small > .knob:after {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 1.2rem;
      height: 1.2rem;
      margin-top: -0.6rem;
      margin-left: -0.6rem;
      content: "";
      background: #90a4ae;
      border-radius: 50%; }
    .hand.small > .knob:before {
      position: absolute;
      bottom: 0;
      left: 50%;
      width: 0.4rem;
      height: 2.2rem;
      margin-left: -0.2rem;
      content: "";
      background: #90a4ae; }

.knob {
  position: absolute;
  top: -3.4rem;
  left: 50%;
  width: 3.4rem;
  height: 3.4rem;
  margin-left: -1.7rem;
  cursor: pointer;
  background-color: #90a4ae;
  border-radius: 50%; }



