.root {
  position: fixed;
  right: 2.4rem;
  bottom: 0;
  left: 2.4rem;
  z-index: 200;
  display: flex;
  align-items: center;
  padding: 1.4rem 2.4rem;
  margin: 0 auto;
  margin-top: 1.4rem;
  color: white;
  background-color: #212121;
  border-radius: 0.2rem;
  transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1) 0.35s; }
  .root.accept .button {
    color: #4caf50; }
