  .root:not(.active) {
    transform: translateY(100%); }
  .root.active {
    transform: translateY(0%); }

.icon {
  margin-right: 1.4rem; }

.label {
  flex-grow: 1;
  font-size: 1.4rem; }

.button {
  min-width: inherit;
