.icon {
  margin-right: 1rem;
  font-size: 1.8rem; }

.root {
  position: relative;
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: center;
  line-height: 1.5;
  cursor: pointer;
  transition: opacity 0.35s cubic-bezier(0.4, 0, 0.2, 1); }
  .root:not(.active) {
    opacity: .5; }
  .root:hover, .root:active {
    opacity: 1; }
  .root > * {
    vertical-align: middle; }
  .root > abbr {
    text-transform: capitalize; }
  .root > small {
    margin-left: 0.8rem;
    font-size: 1.2rem;
    text-align: center; }



