.root {
  position: relative;
  display: flex;
  flex-direction: column; }

.navigation {
  display: flex;
  flex-direction: row;
  box-shadow: inset 0 -1px #eeeeee; }

.label {
  padding: 1.7rem 1.2rem;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1;
  color: rgba(0, 0, 0, 0.7);
  text-transform: uppercase;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.35s;
  transition-property: box-shadow, color; }
  .label.active {
    color: black; }
  .label.disabled {
    opacity: 0.2; }
  .label:not(.disabled) {
    cursor: pointer; }
  .label.hidden {
    display: none; }

.pointer {
  position: absolute;
  width: 0;
  height: 0.2rem;
  margin-top: -0.2rem;
  background-color: #90a4ae;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.35s;
  transition-property: left, width; }

.tab {
  display: flex;
  flex-direction: column;
  padding: 1.7rem 1.2rem; }
  .tab:not(.active) {
    display: none; }
  .tab.active {
    display: block; }



