.avatar {
  position: relative;
  display: inline-block;
  width: 4rem;
  height: 4rem;
  overflow: hidden;
  font-size: 2.4rem;
  color: white;
  text-align: center;
  background-color: #9e9e9e;
  border-radius: 50%; }
  .avatar > svg {
    width: 1em;
    height: 4rem;
    fill: currentColor; }
  .avatar > img {
    max-width: 100%;
    height: auto; }

.image {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: transparent;
  background-position: center;
  background-size: cover;
  border-radius: 50%; }

.letter {
  display: block;
  width: 100%;
  line-height: 4rem; }



